@import "normalize.css";
@font-face {
  font-family: "PlayfairDisplayBlack";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local("PlayfairDisplayBlack"), url("fonts/PlayfairDisplay-Black.woff2") format("woff2"), url("fonts/PlayfairDisplay-Black.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("OpenSansRegular"), url("fonts/open-sans-v35-cyrillic_latin-regular.woff2") format("woff2"), url("fonts/open-sans-v35-cyrillic_latin-regular.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("OpenSansSemiBold"), url("fonts/open-sans-v35-cyrillic_latin-600.woff2") format("woff2"), url("fonts/open-sans-v35-cyrillic_latin-600.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("OpenSansBold"), url("fonts/open-sans-v35-cyrillic_latin-700.woff2") format("woff2"), url("fonts/open-sans-v35-cyrillic_latin-700.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  outline: none;
  color: black;
}
a:focus-visible {
  text-decoration: underline;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img,
svg {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  min-width: 320px;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.4;
  background-color: white;
  color: black;
}

.container {
  max-width: 1280px;
  padding: 0 50px;
  margin: 0 auto;
}

.btn {
  padding: 0;
  cursor: pointer;
  background-color: white;
}

@keyframes blink {
  0% {
    transform: scaleY(0.5);
  }
  10% {
    transform: scaleY(1);
  }
  15% {
    transform: scaleY(0.5);
  }
  20% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes line {
  0% {
    transform: scaleX(0);
  }
  7% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(1);
  }
}
@keyframes cursor {
  0% {
    background-color: black;
  }
  50% {
    background-color: transparent;
  }
  100% {
    background-color: black;
  }
}
@keyframes up {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-10px);
  }
}
@keyframes down {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(10px);
  }
}
@keyframes swing {
  0% {
    transform: rotate(-10deg);
  }
  30% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes right {
  0% {
    transform: translateX(20px);
  }
  80% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(0);
  }
}
@media (any-hover: hover) {
  a:hover {
    text-decoration: underline;
  }
}
@media (any-hover: hover) and (min-width: 963px) {
  .header__link:hover {
    text-decoration: none;
  }
  .header__link:hover::after {
    transform: scaleX(1);
  }
}
@media (any-hover: hover) {
  .greeting__btn:hover .greeting__svg {
    fill: #93ADBF;
  }
  .portfolio__card-wrapper:hover .portfolio__top::before {
    opacity: 0.8;
  }
  .portfolio__card-wrapper:hover .portfolio__desc {
    opacity: 1;
    visibility: visible;
  }
  .portfolio__card-wrapper:hover .portfolio__name {
    text-decoration: underline;
  }
  .portfolio__card-wrapper:hover .portfolio__svg {
    animation: right 1s ease-in-out infinite;
    animation-direction: alternate-reverse;
  }
  .portfolio__github-link:hover {
    text-decoration: none;
  }
  .portfolio__btn:hover {
    background-color: black;
    border-color: black;
  }
  .portfolio__card-btn:hover {
    background-color: black;
  }
  .skills__item:hover {
    transform: scale(1.1) !important;
  }
  .contacts__item:hover .contacts__text:last-of-type {
    text-decoration: underline;
  }
  .contacts__item:hover .contacts__arrow {
    animation: right 1s ease-in-out infinite;
    animation-direction: alternate-reverse;
  }
  .contacts__link:hover {
    text-decoration: none;
  }
  .scroll__btn:hover .scroll__svg {
    fill: #93ADBF;
  }
}
.section {
  padding: 85px 0;
}
.section__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.section__title {
  margin-bottom: 40px;
  font-size: 26px;
  font-weight: 700;
}

.header {
  width: 100%;
  position: fixed;
  z-index: 100;
  background-color: white;
}
.header .container {
  height: 100%;
}
.header__list {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}
.header__item {
  width: 100%;
  grid-column: span 3;
}
.header__link {
  padding: 30px 0;
  display: block;
  justify-content: center;
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  position: relative;
  text-align: center;
}
.header__link::after {
  content: "";
  width: 100%;
  bottom: 0;
  left: 0;
  height: 3px;
  position: absolute;
  background-color: #93ADBF;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.2s ease-in-out;
}
.header__link:focus-visible {
  text-decoration: none;
}
.header__link:focus-visible::after {
  transform: scaleX(1);
}
.header__burger {
  display: none;
}

.greeting {
  padding: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.greeting__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.greeting__content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
.greeting__left {
  margin-right: 40px;
}
.greeting__right {
  position: relative;
}
.greeting__animate {
  position: absolute;
  max-width: 110px;
  width: 100%;
  max-height: 45px;
  height: 100%;
  left: 50%;
  top: 71%;
  transform: translateX(-50%) translateY(-29%);
}
.greeting__animate::before {
  content: "";
  position: absolute;
  width: 140%;
  height: 180%;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: #93ADBF;
  border-radius: 10px;
  opacity: 0.2;
}
.greeting__animate-line {
  position: absolute;
  right: 0;
  display: block;
  width: 100%;
  height: 4.5px;
  background-color: black;
  transform-origin: 100%;
  animation: line 2.5s ease infinite;
  opacity: 0.4;
}
.greeting__animate-line:nth-child(1) {
  top: 0;
}
.greeting__animate-line:nth-child(2) {
  top: 33.33%;
  animation-delay: 0.2s;
}
.greeting__animate-line:nth-child(3) {
  width: 50%;
  top: 66.66%;
  animation-delay: 0.45s;
}
.greeting__animate-line:nth-child(4) {
  width: 80%;
  top: 100%;
  animation-delay: 0.65s;
}
.greeting__img-wrapper {
  display: flex;
  max-width: 320px;
  position: relative;
}
.greeting__img-wrapper::after {
  content: "";
  position: absolute;
  top: 31.5%;
  left: 41%;
  display: block;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background-color: black;
  animation: blink 2s ease-in-out infinite;
  animation-direction: alternate-reverse;
}
.greeting__img-wrapper::before {
  content: "";
  position: absolute;
  top: 31.5%;
  right: 41%;
  display: block;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background-color: black;
  animation: blink 2s linear infinite;
  animation-direction: alternate-reverse;
}
.greeting__span {
  display: block;
  margin-bottom: 10px;
  position: relative;
  font-size: 18px;
  font-weight: 700;
}
.greeting__title {
  margin: 0;
  display: inline-block;
  font-size: 40px;
  font-weight: 700;
  position: relative;
}
.greeting__title::after {
  content: "";
  position: absolute;
  right: -10px;
  width: 4px;
  height: 100%;
  background-color: black;
  animation: cursor 1.5s step-end infinite;
}
.greeting__btn {
  position: relative;
  border: none;
  outline: none;
  border-radius: 100%;
  animation: down 1s ease-in-out infinite;
  animation-direction: alternate-reverse;
}
.greeting__btn:focus-visible .greeting__svg {
  fill: #93ADBF;
}
.greeting__svg {
  width: 50px;
  height: 50px;
  fill: black;
  display: flex;
  transition: fill 0.1s ease-in-out;
}

.info {
  height: 100vh;
  background-color: #E9EFF2;
}
.info a {
  font-weight: 700;
}
.info__text {
  text-align: center;
  max-width: 800px;
}
.info__text:not(:last-child) {
  margin-bottom: 15px;
}
.info__span {
  font-weight: 700;
}

.portfolio__text {
  margin-bottom: 30px;
}
.portfolio__list {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(12, 1fr);
  gap: 85px;
  margin-bottom: 40px;
}
.portfolio__item {
  grid-column: span 4;
}
.portfolio__item:not(:nth-child(-n+3)) {
  display: none;
}
.portfolio__item.is-visible {
  display: block;
}
.portfolio__card {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.portfolio__card-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.portfolio__card-wrapper:focus-within .portfolio__top::before {
  opacity: 0.8;
}
.portfolio__card-wrapper:focus-within .portfolio__desc {
  opacity: 1;
  visibility: visible;
}
.portfolio__card-wrapper:focus-within .portfolio__name {
  text-decoration: underline;
}
.portfolio__card-wrapper:focus-within .portfolio__svg {
  animation: right 1s ease-in-out infinite;
  animation-direction: alternate-reverse;
}
.portfolio__top {
  position: relative;
  border: 1px #c6c6c6 solid;
}
.portfolio__top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0;
  transition: opacity 0.1s ease-in-out;
}
.portfolio__img-wrapper {
  display: flex;
}
.portfolio__desc {
  width: 100%;
  padding: 20px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: white;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.1s ease-in-out, visibility 0.1s ease-in-out;
}
.portfolio__content {
  padding: 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.portfolio__name-author {
  margin-bottom: 18px;
}
.portfolio__name {
  margin-bottom: 6px;
  font-weight: 700;
}
.portfolio__author {
  display: block;
  color: #c6c6c6;
}
.portfolio__btn {
  padding: 20px;
  background-color: #c6c6c6;
  color: white;
  outline: none;
  font-weight: 600;
  border-radius: 0;
  border: 3px solid #c6c6c6;
  transition: background-color 0.1s ease-in-out, border-color 0.1s ease-in-out;
}
.portfolio__btn:focus-visible {
  background-color: black;
  border-color: black;
}
.portfolio__btn.is-hidden {
  display: none;
}
.portfolio__github-link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.portfolio__github-link::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.portfolio__github-link:focus-visible {
  text-decoration: none;
}
.portfolio__svg {
  display: inline-block;
  max-height: 15px;
  width: 36px;
}
.portfolio__span {
  display: inline-block;
  margin-right: 10px;
  font-weight: 700;
}
.portfolio__card-btn {
  width: 100%;
  border: none;
  color: white;
  padding: 15px;
  font-weight: 700;
  background-color: #93ADBF;
  transition: background-color 0.1s ease-in-out;
}
.portfolio__card-btn:focus-visible {
  background-color: black;
}

.skills__list {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(12, 1fr);
  margin-bottom: 100px;
  gap: 65px;
}
.skills__item {
  grid-column: span 4;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.1s ease-in-out;
}
.skills__text {
  text-align: center;
}
.skills__svg {
  margin-bottom: 25px;
  width: auto;
  height: 50px;
  display: flex;
  transition: transform 0.1s ease-in-out;
}
.skills__span {
  font-weight: 700;
}

.contacts {
  height: 100vh;
  background-color: #F2F2F2;
}
.contacts__list {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 85px;
}
.contacts__item {
  width: auto;
  padding: 40px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.contacts__item:focus-within .contacts__text:last-of-type {
  text-decoration: underline;
}
.contacts__item:focus-within .contacts__arrow {
  animation: right 1s ease-in-out infinite;
  animation-direction: alternate-reverse;
}
.contacts__text:first-of-type {
  margin-bottom: 5px;
}
.contacts__text:last-of-type {
  margin-bottom: 40px;
  font-weight: 700;
}
.contacts__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.contacts__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.contacts__link:focus-visible {
  text-decoration: none;
}
.contacts__span {
  display: inline-block;
  margin-right: 10px;
  font-weight: 700;
}
.contacts__arrow {
  display: inline-block;
  max-height: 15px;
  width: 36px;
}
.contacts__svg {
  width: auto;
  height: 50px;
  margin-bottom: 30px;
  display: block;
}

.footer {
  padding: 85px 0 30px 0;
}
.footer__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.footer__span {
  display: block;
  text-align: center;
  opacity: 0.5;
}

.scroll {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 40px;
}
.scroll__btn {
  position: relative;
  border: none;
  border-radius: 100%;
  animation: up 1s ease-in-out infinite;
  animation-direction: alternate-reverse;
  outline: none;
}
.scroll__btn:focus-visible .scroll__svg {
  fill: #93ADBF;
}
.scroll__svg {
  width: 50px;
  height: 50px;
  fill: black;
  display: flex;
  transition: fill 0.1s ease-in-out;
}

@media (max-width: 1200px) {
  .portfolio__list {
    grid-template-columns: repeat(6, 1fr);
    gap: 40px;
  }
  .portfolio__item {
    grid-column: span 3;
  }
  .portfolio__item:not(:nth-child(-n+2)) {
    display: none;
  }
  .portfolio__item.is-visible {
    display: block;
  }
  .skills__list {
    grid-template-columns: repeat(6, 1fr);
  }
  .skills__item {
    grid-column: span 3;
  }
  .skills__item-secondary {
    max-width: 530px;
  }
}
@media (max-width: 962px) {
  .container {
    padding: 0 35px;
  }
  .is-opened.header {
    background-color: white;
    transition: all 0.1s ease-in-out;
  }
  .is-closed .header__burger-line {
    opacity: 0;
  }
  .is-closed.header__burger::before {
    top: 50%;
    transform: rotate(45deg);
  }
  .is-closed.header__burger::after {
    top: 50%;
    bottom: auto;
    transform: rotate(-45deg);
  }
  .header {
    padding: 30px 0;
    background-color: transparent;
  }
  .header::before {
    content: "";
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    display: block;
    background-color: white;
    width: 80px;
  }
  .header__burger {
    width: 26px;
    height: 23px;
    display: flex;
    border: none;
    outline: none;
    position: relative;
    background-color: transparent;
  }
  .header__burger::before {
    content: "";
    width: 100%;
    height: 3px;
    border-radius: 2px;
    background-color: black;
    position: absolute;
    top: 0;
    left: 0;
    transition: transform 0.1s ease-in-out, top 0.1s ease-in-out, bottom 0.1s ease-in-out;
  }
  .header__burger::after {
    content: "";
    width: 100%;
    height: 3px;
    border-radius: 2px;
    background-color: black;
    position: absolute;
    left: 0;
    bottom: 0;
    transition: transform 0.1s ease-in-out, top 0.1s ease-in-out, bottom 0.1s ease-in-out;
  }
  .header__burger-line {
    width: 100%;
    height: 3px;
    border-radius: 2px;
    background-color: black;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: opacity 0.1s ease-in-out;
  }
  .header__nav {
    padding: 85px;
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    border-bottom: 1px #c6c6c6 solid;
    border-top: 1px #c6c6c6 solid;
    visibility: hidden;
    transform: translateX(-50%);
    z-index: 50;
    overflow-y: auto;
    transition: visibility 0.1s ease-in-out, transform 0.1s ease-in-out;
  }
  .header__nav.is-active {
    transform: translateX(0);
    visibility: visible;
  }
  .header__list {
    grid-template-columns: repeat(6, 1fr);
    gap: 40px;
  }
  .header__item {
    grid-column: span 6;
  }
  .header__link {
    padding: 0;
  }
  .header__link::after {
    display: none;
  }
  .header__link:focus-visible {
    text-decoration: underline;
  }
  .greeting__content {
    flex-direction: column-reverse;
  }
  .greeting__right {
    margin-bottom: 20px;
  }
  .greeting__left {
    margin-right: 0;
    margin-bottom: 30px;
    text-align: center;
  }
  .skills__list-secondary {
    flex-direction: column;
    align-items: center;
  }
  .skills__item-secondary {
    width: 100%;
    max-width: 450px;
  }
  .skills__item-secondary:first-child {
    margin-right: 0;
    margin-bottom: 40px;
  }
  .info {
    height: auto;
  }
  .contacts {
    height: auto;
  }
  .contacts__list {
    gap: 40px;
  }
}
@media (max-width: 750px) {
  .header__nav {
    padding: 50px;
  }
  .contacts__list {
    max-width: 430px;
    flex-direction: column;
  }
  .portfolio__item {
    grid-column: span 6;
  }
  .skills__list {
    gap: 40px;
  }
  .skills__item-secondary {
    max-width: 400px;
  }
}
@media (max-width: 576px) {
  body {
    font-size: 16px;
  }
  .container {
    padding: 0 25px;
  }
  .section {
    padding: 70px 0;
  }
  .section__title {
    font-size: 22px;
    text-align: center;
  }
  .header__nav {
    padding: 50px 40px;
  }
  .greeting__animate {
    max-width: 94px;
    max-height: 38px;
  }
  .greeting__animate-line {
    height: 4px;
  }
  .greeting__img-wrapper {
    max-width: 260px;
  }
  .greeting__img-wrapper::after {
    width: 9.2px;
    height: 9.2px;
  }
  .greeting__img-wrapper::before {
    width: 9.2px;
    height: 9.2px;
  }
  .greeting__title {
    font-size: 27px;
  }
  .greeting__title::after {
    width: 3px;
  }
  .greeting__svg {
    width: 47px;
    height: 47px;
  }
  .portfolio__text {
    text-align: center;
  }
  .skills__list {
    display: flex;
    flex-direction: column;
    gap: 75px;
  }
  .skills__item {
    grid-column: span 6;
  }
  .scroll__svg {
    width: 47px;
    height: 47px;
  }
}